
import React, {useEffect, useState} from 'react';
import "../../css/styles.css";
import AdminLoginRegiFooter from "./footer";
import * as common from "../../comm/common";
import DaumPostcode from 'react-daum-postcode';
import Modal from "../common/modal";
import {Link} from "react-router-dom";
import {useDispatch} from "react-redux";
import {showAlertModal} from "../../action/alertModal";

const  AdminRegister = () => {
    const dispatch = useDispatch();

    let idCheckStatus = "";

    // useState  open . (open true   )
    const [modalOpen, setModalOpen] = useState(false);
    const [isOpenPost, setIsOpenPost] = useState(false);

    const onChangeOpenPost = () => {
        setIsOpenPost(!isOpenPost);
    };

    const onCompletePost = (data) => {
        let fullAddr = data.address;
        let extraAddr = '';

        if (data.addressType === 'R') {
            if (data.bname !== '') {
                extraAddr += data.bname;
            }
            if (data.buildingName !== '') {
                extraAddr += extraAddr !== '' ? `, ${data.buildingName}` : data.buildingName;
            }
            fullAddr += extraAddr !== '' ? ` (${extraAddr})` : '';
        }
        document.getElementById("location").value = fullAddr;
        closeModal();
    };

    const openModal = () => {
        setModalOpen(true);
    };

    const closeModal = () => {
        setModalOpen(false);
    };

    useEffect( () => {

    },[]);

    const userIdCheck = () => {
        let data = {
            userId : document.getElementById("userId").value
        };

        common.fetchLoad("/userIdCheck", "POST", data, function (result) {
            idCheckStatus = result.idCheckStatus;
            if(idCheckStatus == "1"){
                document.getElementById("idCheck").innerText = "  .";
                document.getElementById("idCheck").style.color = "red";
            } else {
                document.getElementById("idCheck").innerText = "";
                document.getElementById("idCheck").style.color = "black";
            }
        });
    };

    const fileChange = () => {
        common.uploadImgChange("imageFile", "thumbnailImg");
    }

    const register = () => {

        /*    */
        if(idCheckStatus == "1"){
            dispatch(showAlertModal('  .'));
            return;
        }

        /*   */
        if(document.getElementById("password").value != document.getElementById("passwordchk").value){
            dispatch(showAlertModal('     .'));
            document.getElementById("passwordchk").focus();
            return;
        }

        let data = {
            userId : document.getElementById("userId").value,
            password : document.getElementById("password").value,
            userName : document.getElementById("userName").value,
            age : document.getElementById("age").value,
            gender : document.getElementById("gender").value,
            email : document.getElementById("email").value,
            location : document.getElementById("location").value,
            locationDetail : document.getElementById("locationDtl").value,
            imageFileNo : "1",
            phoneNum : document.getElementById("phoneNum").value,
            rule : "admin",
            deleted : "0"
        };

        /*   */
        let validationChkName = ["", ""
            , "", ""
            , "", ""
            , "", ""
            , ""];

        let validationChkId = ["userId", "password"
            , "userName", "age"
            , "gender" , "email"
            , "location", "locationDtl"
            , "phoneNum"];

        for(let i = 0 ; i < validationChkName.length ; i++){
            if (!common.nullCheck(document.getElementById(validationChkId[i]).value)){
                dispatch(showAlertModal("["+ validationChkName[i] + "] ."));
                document.getElementById(validationChkId[i]).focus();
                return;
            }
        }

        /*   */
        if(document.getElementById("imageFile").value){

            /* */
            new Promise(function(resolve, reject){
                let form = new FormData();
                form.append( "file", document.getElementById("imageFile").files[0]);

                common.fetchLoad("/file/upload", "POST", form, function (result) {
                    resolve(result.uploadList[0].fileNo);
                }, true);

            }).then(function (resolve) {
                /*      .*/
                if (resolve){
                    data.imageFileNo = resolve;
                }
                common.fetchLoad("/registerProcessing", "POST", data, function (result) {
                    if (result.registerStatus == "1"){
                        dispatch(showAlertModal(" ."));
                        window.location.href = "../admin/login.html";
                    }
                });
            });

        } else {  /*     */

            common.fetchLoad("/registerProcessing", "POST", data, function (result) {
                if (result.registerStatus == "1"){
                    dispatch(showAlertModal(" ."));
                    window.location.href = "../admin/login.html";
                }
            });
        }
    }

    const imageStyle = {
        "width" : "100%",
        "border" : "1px solid #ced4da"
    };

   return (
      <div className="bg-primary">
          <div id="layoutAuthentication">
              <div id="layoutAuthentication_content">
                  <main>
                      <div className="container">
                          <div className="row justify-content-center">
                              <div className="col-lg-7">
                                  <div className="card shadow-lg border-0 rounded-lg mt-5">
                                      <div className="card-header"><h3 className="text-center font-weight-light my-4">Admin </h3></div>
                                      <div className="card-body">
                                          <form>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="userId" type="text" placeholder=" " maxLength="20" onChange={userIdCheck}/>
                                                  <label htmlFor="userId" id="idCheck"></label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="password" type="password" placeholder=" " maxLength="20"/>
                                                  <label htmlFor="password"></label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="passwordchk" type="password" placeholder=" " maxLength="20"/>
                                                  <label htmlFor="passwordchk"> </label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="userName" type="text" placeholder=" " maxLength="20"/>
                                                  <label htmlFor="userName"></label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="age" type="text" placeholder=" " maxLength="20"/>
                                                  <label htmlFor="age"></label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <select id="gender" className="form-select">
                                                      <option value=""></option>
                                                      <option value="1"></option>
                                                      <option value="2"></option>
                                                  </select>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="email" type="email" placeholder="name@example.com"/>
                                                  <label htmlFor="email"></label>
                                              </div>
                                              <div className="row mb-3">
                                                  <div className="col-md-8">
                                                      <div className="form-floating mb-3 mb-md-0">
                                                          <input className="form-control" id="location" type="text" placeholder=" "/>
                                                          <label htmlFor="location"></label>
                                                      </div>
                                                  </div>
                                                  <div className="col-md-4">
                                                      <div className="form-floating mb-3 mb-md-0">
                                                          <div className="d-grid">
                                                              <a className="btn btn-primary btn-block" id="btnLocation" onClick={openModal}></a>
                                                          </div>
                                                      </div>
                                                  </div>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="locationDtl" type="text"
                                                         placeholder=" "/>
                                                  <label htmlFor="locationDtl"></label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="phoneNum" type="text"
                                                         placeholder="010-1234-5678" maxLength="30"/>
                                                  <label htmlFor="phoneNum">(-)  </label>
                                              </div>
                                              <div className="form-floating mb-3">
                                                  <input className="form-control" id="imageFile" type="file" accept=".gif, .jpg, .png" onChange={fileChange}/>
                                              </div>
                                              <img id="thumbnailImg" src="" style={imageStyle}/>
                                              <div className="mt-4 mb-0">
                                                  <div className="d-grid">
                                                      <a className="btn btn-primary btn-block" id="btnRegister" onClick={register}></a>
                                                  </div>
                                              </div>
                                          </form>
                                      </div>
                                      <div className="card-footer text-center py-3">
                                          <div className="small"><Link to="/admin/login"></Link></div>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </main>
              </div>
              <AdminLoginRegiFooter/>
          </div>
          <Modal open={modalOpen} close={closeModal} header="">
              <DaumPostcode autoClose onComplete={onCompletePost } />
          </Modal>
      </div>
  );
}
export default AdminRegister;
