import React from 'react';
import '../../css/modal.css';

const Modal = (props) => {
    // , ,     
    const { open, close, header } = props;

    return (
        //   openModal  .
        <div className={open ? 'openModal modal' : 'modal'}>
            {open ? (
                <section>
                    <header>
                        {header}
                        <button className="close" onClick={close}>
                            {' '}
                            &times;{' '}
                        </button>
                    </header>
                    <main>{props.children}</main>
                    <footer>
                        <button className="close" onClick={close}>
                            {' '}
                            {' '}
                        </button>
                    </footer>
                </section>
            ) : null}
        </div>
    );
};

export default Modal;